import interfaces from '../../../utils/interface.js'



export default{
	data(){
		return{
			goodsInfo :[],
			clicked_list:[false,false,false,true,true], //对应星星个数
			des_font : '好',
			imgList: [],
			count: 9, // 限制上传张数
			comment_content : ''
		}
	},
	methods:{
		setStar(index){ //评分

			switch (index) {
				case 0:
					this.star(index)
					this.des_font = '差'
					break;
				case 1:
					this.star(index)
					this.des_font = '一般'
					break;
				case 2:
					this.star(index)
					this.des_font = '好'
					break;
				case 3:
					this.star(index)
					this.des_font = '非常好'
					break;
				case 4:
					this.star(index)
					this.des_font = '五星好评'
					break;
			}
		},
		star(index){ //改变星星状态
			var fil_clicked = [];
		    this.clicked_list.forEach((item , ind) => {
				ind <= index ? item = false : item = true;
				this.clicked_list[ind] = item
			});
		},
		uploadImg(){ //上传照片
			uni.chooseImage({
				count: this.count,
				sizeType: ['original', 'compressed'],
				sourceType: ['album'],
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths);
						this.count =  9 - this.imgList.length;
					} else {
						this.imgList = res.tempFilePaths;
						if(this.imgList.length >= 9) {
							// console.log('大于9')
							this.imgList = res.tempFilePaths.slice(this.imgList.length - 9 , this.imgList.length);//H5
						}
						this.count =  9 - this.imgList.length;
					}
				}
			});
		},
		ViewImage(e) { //显示查看
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) { //删除

			this.imgList.splice(e.currentTarget.dataset.index, 1);
			this.count =  9 - this.imgList.length;
		},
		submit(){ //提交
			console.log('提交评论' , this.imgList)
			var star_count = 0;
			for(var i = 0 ; i < this.clicked_list.length; i++){
				if(this.clicked_list[i] == false){
					star_count += 1; //记录星星数量
				}
			}
			this.request({
				url : interfaces.add_assess,
				method: 'POST' ,
				data : {
					goods_id : 2772 ,
					content : this.comment_content,
					heart : star_count,
				},
				seccess : ((res) => {
					if(res.error_code == 0){
						uni.showToast({
							title: "评论提交成功!"
						},1000)
					}
				})
			})














		}
	},
	onLoad(option){
		this.goodsInfo = JSON.parse(option.goodsInfo);
		// console.log(this.goodsInfo)
	}
}
